<template>
<div id="app">
    <div v-show="isActive==1">
        <h1>List Products</h1>
        <table border="1px solid #ebebeb">
            <tr>
                <th>Type</th>
                <th>Price</th>
                <th></th>
            </tr>
            <tr v-for="product in products" :key="product.code">
                <td>{{product.name}}</td>
                <td>{{product.price}}</td>
                <td><button @click="addToCart(product)">Add to Cart</button></td>
            </tr>
        </table>
        <h1>List product you added </h1>
        <table border="1px solid #ebebeb">
            <tr>
                <th>Type</th>
                <th>Price</th>
                <th>Quantity</th>
                <th>Del</th>
            </tr>
            <tr v-show="cartProDucts.length ==0">
                <td colspan="4"> You don't add anything</td>
            </tr>
            <tr v-show="cartProDucts.length >0" v-for="(cartProDuct,index) in cartProDucts" :key="index">

                <td>{{cartProDuct.name}}</td>
                <td>{{cartProDuct.price}}</td>
                <td><button @click="cartProDuct.quantity -=1">-</button><input type="text" v-model="cartProDuct.quantity" name="" id=""> <button @click="cartProDuct.quantity +=1">+</button></td>

                <td><button @click="DelProduct(cartProDuct)">Del</button></td>
            </tr>
            <tr v-show="cartProDucts.length >0">
                <td colspan="4">TotalPrice : {{cartOrder}} VND</td>

                <!-- <td colspan="4">{{}}</td> -->
            </tr>
            <tr v-show="cartOrder > 25000000">
              <td colspan="4" >Discuont:5%</td>
              
            </tr>
            <tr  v-show="cartOrder > 40000000">
              <td colspan="4">Discuont:8%</td>
            </tr>

            <tr v-show="cartProDucts.length >0">
                <td colspan="4">TotalPay :{{payTotal}}</td>
            </tr>
            

        </table>
    </div>
    <button @click="isActive=2">Pay</button>
    <div v-show="isActive==2">
      <form action="">
      <p>Your Name: <input type="text" name="" id=""></p>
      <p>Your Mail: <input type="text" name="" id=""></p>
      <p>Your Password: <input type="text" name="" id=""></p>
      <button>Submit</button>
      </form>
    </div>

</div>
</template>

<script>
export default {
    data() {
        return {

            products: [{
                    code: "sp1",
                    name: "Iphone 1",
                    price: 1000000,
                    quantity: 1
                },
                {
                    code: "sp2",
                    name: "Iphone 2",
                    price: 2000000,
                    quantity: 1
                },
                {
                    code: "sp3",
                    name: "Iphone 3",
                    price: 3000000,
                    quantity: 1
                },
                {
                    code: "sp4",
                    name: "Iphone 4",
                    price: 4000000,
                    quantity: 1
                },
                {
                    code: "sp5",
                    name: "Iphone 5",
                    price: 5000000,
                    quantity: 1
                },
                {
                    code: "sp6",
                    name: "Iphone 6",
                    price: 6000000,
                    quantity: 1
                },
            ],
            cartProDucts: [],
            payTotal : 0,
            isActive:1,
        }
    },
    methods: {
        addToCart(product) {
            if (this.cartProDucts.indexOf(product) != -1) {
                alert("You added this products");
                return;
            }
            this.cartProDucts.push(product);
        },
        DelProduct(cartProDucts) {

            this.cartProDucts.splice(cartProDucts, 1);
        },
        caculatorPrice() {
            let cartOrder = 0;
            this.cartProDucts.forEach((product) => {

                cartOrder += product.price * product.quantity;
            });
            this.cartOrder = cartOrder;
        //discount price
       if(cartOrder > 25000000){
         this.payTotal = cartOrder * ((100-0.05)/100);
       }
       if(cartOrder >40000000){
         this.payTotal = cartOrder * (8/100);
       }

        },
         

    },
    watch: {
        cartProDucts: {
            handler() {
                this.caculatorPrice()
            },
            deep: true,
            immediate: true,
        },

    }
}
</script>

<style>
table {
    width: 100%;
    border-collapse: collapse;
}
</style>
